    <nav class="commoditynav">
      <van-swipe v-if="swipepd" :loop="false" :width="270" :height="390" ref="swipe1" :show-indicators="false">
        <van-swipe-item class="commodityimg" v-for="(n, i) in commodityDdetails?.topImages" :key="i">
          <img :src="n + '_999x999.v1c0.81.webp'" alt="" />
        </van-swipe-item>
      </van-swipe>

      <van-swipe v-if="!swipepd" :loop="false" :width="270" ref="swipe2" :show-indicators="false">
        <van-swipe-item class="commodityimg" v-for="n in commodityDdetails?.skuBarInfo?.list" :key="n.stockId">
          <img :src="n.image + '_999x999.v1c0.81.webp'" alt="" />
        </van-swipe-item>
