      <div class="commoditynav-b" v-if="commodityDdetails">
        <div class="commoditynav-b-l" @click="swipepd = true">
          <div class="commoditynav-b-l-l">
            <img :src="
              commodityDdetails?.normalShareInfo?.imageUrl +
              '_999x999.v1c0.81.webp'
            " alt="" />
          </div>
          <div class="commoditynav-b-l-r">颜色规格</div>
        </div>
        <ul class="commoditynav-b-r">
          <li v-for="(n, i) in commodityDdetails?.skuBarInfo?.list" :key="n.stockId" @click="chengg(i)">
