        }}</span>
      </p>
      <p>已售{{ commodityDdetails?.normalPrice?.sales }}</p>
    </div>
    <div class="outers" v-if="PromotionList?.outers?.length">
      <p>{{ PromotionList?.outers[0].text }}</p>
      <p>领券
        <van-icon name="arrow" />
      </p>
