      <div class="icon">
        <img v-if="commodityDdetails?.itemInfo?.pickforme?.icon"
          v-lazy="commodityDdetails?.itemInfo?.pickforme?.icon" />
        <p>{{ commodityDdetails?.itemInfo?.pickforme?.name }}</p>
