    </div>

    <div class="bgbar"></div>

    <div class="sizecolor" @click="showmask = true">
      <div class="text">
        <p>选择</p>
        <p v-show="!color && !size" v-for="item in commodityDdetails?.skuInfo?.props" :key="item.label">
