        </p>
        <div class="select" v-show="color || size">
          <p>已选：</p>
          <p>
            <span v-if="!color">颜色</span><span v-else class="active">“{{ color }}”</span>
          </p>
          <p>
            <span v-if="!size">尺码</span><span v-else class="active">“{{ size }}”</span>
          </p>
        </div>
      </div>
      <div class="icon">
        <van-icon name="arrow" />
      </div>
    </div>
    <div class="express">
      <p v-if="commodityDdetails?.itemServices.columns[1]">
        {{ commodityDdetails?.itemServices?.columns[1].desc }}
      </p>
      <p v-if="commodityDdetails?.itemServices?.columns[2]">
        {{ commodityDdetails?.itemServices?.columns[2].desc }}
      </p>
    </div>

    <div class="server">
      <div class="text">
        <p>服务</p>
        <p>
          {{ commodityDdetails?.itemServices?.services[0].name }}
        </p>
        <p>
          {{ commodityDdetails?.itemServices?.services[1].name }}
        </p>
        <p>
          {{ commodityDdetails?.itemServices?.services[2].name }}
        </p>
      </div>
      <div class="icon">
        <van-icon name="arrow" />
      </div>
    </div>

    <div class="comment" v-if="commodityDdetails?.rateInfoV2?.cRate">
      <div class="titles">
        <p>评论({{ commodityDdetails?.rateInfoV2?.cRate }})</p>
        <p>
          评分 <span>{{ commodityDdetails?.rateInfoV2?.cScore }}</span>
        </p>
      </div>
      <div class="rateTags">
        <p v-for="item in commodityDdetails?.rateInfo?.rateTags" :key="item.value">
          {{ item.value }}({{ item.num }})
        </p>
      </div>
      <van-swipe :loop="false" :width="310" class="list" :show-indicators="false">
        <van-swipe-item v-for="item in commodityDdetails?.rateInfoV2?.list" :key="item.rateId">
          <div class="card">
            <div class="head">
              <img v-lazy="item.user.avatar" />
              <div class="name">
                <p>{{ item.user.uname }}</p>
                <p>
                  {{ handleTime(item.created * 1000) }}
                </p>
              </div>
            </div>
            <div class="content">
              <p>{{ item.content }}</p>
            </div>
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>

    <div class="bgbar"></div>

    <div class="shop" v-if="commodityDdetails?.shopInfo">
      <div class="head">
        <img v-lazy="commodityDdetails?.shopInfo?.shopLogo" />
        <div class="msg">
          <p>
            <span>{{ commodityDdetails?.shopInfo?.name }}</span>
            <span>进店
              <van-icon name="arrow" />
            </span>
          </p>
          <div class="saleDescV2">
            <img src="../assets/commodityDdetails-redstart.png" />|
            <p>{{ commodityDdetails?.shopInfo?.saleDescV2 }}</p>
          </div>
          <div class="num">
            <p v-for="item in commodityDdetails?.shopInfo?.shopLabels" :key="item.id">
              {{ item?.text }}
            </p>
          </div>
        </div>
      </div>
      <div class="info"></div>
    </div>

    <div class="bgbar"></div>

    <div class="detailInfo">
