        <p class="active">图文详情</p>
        <p><a href="#parameters">商品参数</a></p>
      </div>
      <div class="detailImage">
        <p>{{ commodityDdetails?.detailInfo?.desc }}</p>
        <img v-for="(img, index) in commodityDdetails?.detailInfo?.detailImage[0]
        .list" :key="index" v-lazy="img" />
      </div>
      <div class="parameters" id="parameters">
        <p>商品详情</p>
        <div class="itemParams">
          <div class="item">
            <p v-for="(value, key) in commodityDdetails?.itemParams?.info?.set" :key="key">
              <span>{{ key }}</span> <span>{{ value }}</span>
            </p>
          </div>
          <p>查看全部
            <van-icon name="arrow" />
          </p>
        </div>
      </div>
    </div>

    <div class="footer">
      <div class="btn">
        <van-icon name="shop-o" />
        <p>店铺</p>
      </div>
      <div class="btn">
        <van-icon name="chat-o" />
        <p>客服</p>
      </div>
      <div class="btn" v-if="showFav" @click="showFav = !showFav">
        <van-icon name="star-o" />
        <p>收藏</p>
      </div>
      <div class="btn" v-else @click="showFav = !showFav">
        <van-icon name="star" class="active" />
        <p>已收藏</p>
      </div>
      <div class="btn" @click="showmask = true">
        <p>加入购物车</p>
      </div>
      <div class="btn" @click="showmask = true">
        <p>立即购买</p>
      </div>
    </div>

    <div class="home" @click="$router.push({ path: '/' })">
      <van-icon name="wap-home-o" />
    </div>

    <div class="shopcart" @click="$router.push({ path: '/shopcart' })">
      <van-icon name="shopping-cart-o" />
    </div>

    <div class="ColorSizeMask" v-if="showmask">
      <div class="mask" @click="showmask = false"></div>
      <div class="msg">
        <div class="hasSelect">
          <div class="imgs">
            <div class="img" v-if="commodityDdetails?.webImOptionInfo?.option?.img || src" v-lazy:background-image="
              color ? src : commodityDdetails?.webImOptionInfo?.option?.img
            " />
          </div>
          <div class="content">
            <div class="icon">
              <p>{{ commodityDdetails?.skuInfo?.priceRange }}</p>
              <van-icon name="cross" @click="showmask = false" />
            </div>
            <!-- 库存 -->
            <p>
              库存：{{
              !color && !size
              ? commodityDdetails?.skuInfo?.totalStock
              : color && size
              ? allnum
              : allsizenum || allcolornum
              }}件
            </p>
            <div class="select">
              <p v-if="!color && !size">请选择 尺码 颜色</p>
              <div v-else>
                <p>已选：</p>
                <p>
                  <span v-if="!color">请选择 颜色</span>
                  <span v-else class="active"> “{{ color }}”</span>
                </p>
                <p v-if="commodityDdetails?.skuInfo?.props[1]">
                  <span v-if="!size">请选择 尺码</span><span v-else class="active">“{{ size }}”</span>
                </p>
              </div>
            </div>
          </div>
        </div>

        <div class="all">
          <div class="color">
            <p>{{ commodityDdetails?.skuInfo?.props[0].label }}</p>
            <div class="type">
              <span v-for="name in commodityDdetails?.skuInfo?.props[0].list" :key="name.index" @click="
                color === name.name ? (color = '') : (color = name.name)
              " :class="color === name.name ? 'active' : ''">
                {{ name.name }}
              </span>
            </div>
          </div>

          <div class="size" v-if="commodityDdetails?.skuInfo?.props[1]">
            <p>{{ commodityDdetails?.skuInfo?.props[1].label }}</p>
            <div class="type">
              <span v-for="name in commodityDdetails?.skuInfo?.props[1].list" :key="name.index"
                @click="size === name.name ? (size = '') : (size = name.name)"
                :class="size === name.name ? 'active' : ''">
                {{ name.name }}
              </span>
            </div>
          </div>

          <div class="num">
            <p>数量</p>
            <div class="btn">
              <button @click="nums <= 1 ? (nums = 1) : nums--" :class="nums <= 1 ? 'disactive' : ''">
                -
              </button>
              <p>{{ nums }}</p>
              <button @click="nums++">+</button>
            </div>
          </div>
        </div>
        <div class="footerbtn">
          <div class="fl-btn" @click="goodsMsg">加入购物车</div>
          <div class="fr-btn">立即购买</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      itemid: this.$route.query.id,
      swipepd: true,
      showFav: true,
      showmask: false,
      nums: 1,
      size: "",
      color: "",
      src: null,
      allsizenum: "",
      allcolornum: "",
    };
  },
  created() {
    this.$store.dispatch("getcommodityDdetails", this.itemid);
    this.$store.dispatch("getPromotionList", this.itemid);
  },
  computed: {
    ...mapState(["commodityDdetails", "PromotionList", "cartlist"]),
    allnum: function () {
      if (this.color && this.size) {
        return this.commodityDdetails?.skuInfo?.skus.filter(
          (o) => o.size === this.size && o.style === this.color
        )[0].stock;
      } else {
        return "";
      }
    },
  },

  methods: {
    ...mapMutations(["getcartlist"]),
    chengg(i) {
      this.swipepd = false;
      setTimeout(() => {
        this.$refs.swipe2.swipeTo(i);
      }, 0);
    },

    handleTime(time) {
      let date = new Date(time);
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      return `${year}-${month}-${day}`;
    },

    goodsMsg() {
      if (this.commodityDdetails?.skuInfo?.props.length === 1 && this.color) {
        this.getcartlist({
          nums: this.nums,
          title: this.commodityDdetails?.itemInfo?.title,
          price: this.commodityDdetails?.normalPrice?.nowPrice,
          src: this.src,
          color: this.color || "",
          id: this.cartlist.length
            ? Math.max(...this.cartlist.map((o) => o.id)) + 1
            : 1,
        });
      }
      if (
        this.commodityDdetails?.skuInfo?.props.length === 2 &&
        this.color &&
        this.size
      ) {
        this.getcartlist({
          nums: this.nums,
          title: this.commodityDdetails?.itemInfo?.title,
          price: this.commodityDdetails?.normalPrice?.nowPrice,
          src: this.src,
          color: this.color || "",
          size: this.size || "",
          id: this.cartlist.length
            ? Math.max(...this.cartlist.map((o) => o.id)) + 1
            : 1,
        });
      }
    },
  },
  watch: {
    color: function (newcolor) {
      // 计算单个颜色的库存

      if (newcolor) {
        this.allcolornum = this.commodityDdetails?.skuInfo?.skus
          .filter((o) => o.style === newcolor)
          .map((o) => o.stock)
          .reduce((total, currentValue) => total + currentValue);
      } else {
        this.allcolornum = "";
      }

      // 获取图片
      if (this.commodityDdetails?.skuBarInfo?.list && newcolor) {
        this.src = this.commodityDdetails?.skuBarInfo?.list.filter(
          (o) => o.label === newcolor
        )[0].image;
      } else {
        this.src = this.commodityDdetails?.webImOptionInfo?.option?.img
      }

    },

    size: function (newsize) {
      if (this.size) {
        this.allsizenum = this.commodityDdetails?.skuInfo?.skus
          .filter((o) => o.size === newsize)
          .map((o) => o.stock)
          .reduce((total, currentValue) => total + currentValue);
      } else {
        this.allsizenum = "";
      }
    },

    cartlist: function (goodsmsglist) {
      localStorage.setItem("cartlist", JSON.stringify(goodsmsglist));
    },
  },
};
</script>

